<template>
  <div>
     <div id="container">
		<!-- logo -->
		<div class=logo></div>
		<!--  -->
	  <div class="black">
	    <form action="data/login.php">
		   <table>
		    <tr>
			   <td>
			     <h4></h4>
			   </td>
			   <td>
			     <p><a href="#"></a></p>
			   </td>	
			 	</tr>
		    <tr>
			   <td colspan="2"><hr></td>
			 	</tr>
		    <tr>
			   <td colspan="2">
			     <input type="text" class="my_width" placeholder="" id="uname" onpress.enter="doLogin" v-model="uname">
			   </td>
			 	</tr>
		    <tr>
			   <td colspan="2">
			     <input type="password" class="my_width" placeholder="" id="upwd" onpress.enter="doLogin" v-model="upwd">
			   </td>
			 	</tr>
		    <tr>
			   <td>
            <div class=mg>
              <input type="checkbox" class="login">
            </div>
			   </td>
			   <td>
           <div class=mg>
              <a href="#"></a>
          </div> 
			   </td>
			  </tr>
		    <tr>
			   <td colspan="2">
			     <input type="submit" class="my_width" id="login"  value="" @click.prevent="doLogin">
			   </td>
			  </tr>
		   </table>
		 	</form>
	  </div> 
  </div>
  </div>
</template>
<script>
import "../assets/css/login.css";
import axios from "axios"; //axios
export default {
  data() {
    return {
      uname: "",
      upwd: ""
    };
  },
  components: {},
  methods: {
    doLogin() {
			
      axios.get(`http://127.0.0.1:3000/user/login?uname=${this.uname}&upwd=${this.upwd}`).then(data => {
          console.log(data);
          if (data.data.code == "1") {
            alert("");
            this.$router.push("/");
          } else {
            alert("");
          }
        }).catch(err => {
          console.log("");
        });
    }
  },
  computed: {},
  mounted() {}
};
</script>
<style scoped>
</style>



