.yx-index .yx-index-Plist .yx-plist .plist-up .plist-up-left a {
  color: #333;
}
.yx-index .yx-index-Plist .yx-plist .plist-up .plist-up-left a:hover {
  color: #b4a078;
}
.yx-index .yx-index-Plist .yx-plist .plist-up .plist-up-right {
  float: right;
  padding-top: 20px;
}
.yx-index .yx-index-Plist .yx-plist .plist-up .plist-up-right ul li {
  float: left;
  display: inline-block;
  margin-left: 10px;
}
.yx-index .yx-index-Plist .yx-plist .plist-up .plist-up-right ul li a {
  color: #333;
}
.yx-index .yx-index-Plist .yx-plist .plist-up .plist-up-right ul li a:hover {
  color: #b4a078;
}
.yx-index .yx-index-Plist .yx-plist .plist-down {
  padding: 0px;
  margin-top: 10px;
  text-align: center;
  /*ring  */
  display: flex;
  justify-content: space-between;
}
.yx-index .yx-index-Plist .yx-plist .plist-down .item {
  float: left;
}
.yx-index .yx-index-Plist .yx-plist .plist-down .item h4 {
  padding-bottom: 0px;
  margin-bottom: 0px;
}
.yx-index .yx-index-Plist .yx-plist .plist-down .item a {
  font-family: "Microsoft Yahei";
  color: #333;
  font-size: 14px;
}
.yx-index .yx-index-Plist .yx-plist .plist-down .item a:hover {
  color: #b4a078;
}
.yx-index .yx-index-Plist .yx-plist .plist-down .item span {
  color: #333;
  font-size: 12px;
  display: inline-block;
  background: #f28f2d;
  padding: 5px;
  margin-top: 10px;
}
.yx-index .yx-index-Plist .yx-plist .plist-down .item p {
  color: #d4282d;
  font-size: 13px;
  padding: 0px;
  margin: 0px;
}
.yx-index .yx-index-Plist .yx-plist .plist-down .item .item-pic {
  background: #f4f4f4;
}
.yx-index .yx-index-Plist .yx-plist .plist-down .item .item-pic img {
  transition: all 1s linear;
